body {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-image: -webkit-gradient(
    linear,
    left top, right bottom,
    from(rgba(0, 47, 75, .5)),
    to(rgba(220, 66, 37, .5))),
    url(../img/bridge.jpg);
  background-image: -webkit-linear-gradient(
    top left,
    rgba(0, 47, 75, .5),
    rgba(220, 66, 37, .5)),
    url(../img/bridge.jpg);
  background-image: -o-linear-gradient(
    top left,
    rgba(0, 47, 75, .5),
    rgba(220, 66, 37, .5)),
    url(../img/bridge.jpg);
  background-image: linear-gradient(
    to bottom right,
    rgba(0, 47, 75, .5),
    rgba(220, 66, 37, .5)),
    url(../img/bridge.jpg);
  background-repeat: no-repeat;
  background-attachment:fixed;
  /* position: fixed; */
}




