/* projects */

.slider{
  display:-ms-flexbox;
  display:flex;
  height:90%;
  width:100%;
  -webkit-transition-timing-function: ease-out;
       -o-transition-timing-function: ease-out;
          transition-timing-function: ease-out;
  -webkit-transition:.5s;
  -o-transition:.5s;
  transition:.5s;
}

.slide{
  -ms-flex:1 1;
      flex:1 1;
  height: 100%;
  width: 100%;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  margin-right: 10%;
  min-width:100%;
  border-radius: 10px;
  -webkit-box-shadow: 1px 1px 5px black;
          box-shadow: 1px 1px 5px black;
  background-size: cover;
  overflow:hidden;
}

.slide1 {
  background-image: url(../img/Juicebox.png);
  background-position: center;
  height:100%;
  z-index:1;
}

.slide2{
  background-image: url(../img/ToonFlix.png);
  background-position: center;
  height:100%;
}

.slide3 {
  background-image: url(../img/OmniChatBlack.png);
  background-position: center;
  height:100%;
}

.about{
  position:relative;
  background-color:black;
  color:white;
  height:100%;
  z-index: 2;
  opacity:.9;
  -webkit-transition:.6s;
  -o-transition:.6s;
  transition:.6s;
}

.slide:hover .about{
  -webkit-transform:translateY(-100%);
      -ms-transform:translateY(-100%);
          transform:translateY(-100%);
  -webkit-transition:.4s;
  -o-transition:.4s;
  transition:.4s;
}

.description {
  font-size: 16px;
  padding: 10px 40px;
}


.tags_flex{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
}

.tags_container{
  font-size: 25px;
  -ms-flex:1 1;
      flex:1 1;
  background-color:grey;
  border-radius: 2px;
  margin: 10px;
  vertical-align: middle;
  position:relative;
  height: 50px;
  -ms-flex-preferred-size:15%;
      flex-basis:15%;
}

.tags{
  text-align: center;
  vertical-align: middle;
  position: absolute;
  width: 100%;
  top:32%;
}

.description{
  position:relative;
  padding: 6% 15%;
}

.button_container{
  position:absolute;
  left:50%;
  bottom: 10%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}

.button_toonflix{
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}

.slidecursor {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack:center;
      justify-content:center;
  -ms-flex-line-pack: center;
      align-content: center;
  width:100%;
  bottom:0;
  height: 10%;
}

.cursor {
  text-align: center;
  background-color: transparent;
  border: none;
  color: white;
  font: 16px;
  font-weight: 400;
  
}

.prev:focus{
  outline:none;
}
.next:focus{
  outline:none;
}

.prev {
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-negative: 1;
      flex-shrink: 1;
  -ms-flex-preferred-size: 0;
      flex-basis: 0;
}

.next{
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-negative: 1;
      flex-shrink: 1;
  -ms-flex-preferred-size: 0;
      flex-basis: 0;
}



